import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { ThumbsUp, ThumbsDown, CheckCircle, MessageSquare, User, Clock } from 'lucide-react';
import axios from 'axios';
import toast from 'react-hot-toast';
import { useAuth } from '../contexts/AuthContext';

const QuestionDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [question, setQuestion] = useState(null);
  const [answers, setAnswers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [answerContent, setAnswerContent] = useState('');
  const [submittingAnswer, setSubmittingAnswer] = useState(false);

  useEffect(() => {
    fetchQuestionAndAnswers();
  }, [id, fetchQuestionAndAnswers]);

  const fetchQuestionAndAnswers = async () => {
    try {
      setLoading(true);
      const [questionResponse, answersResponse] = await Promise.all([
        axios.get(`/api/questions/${id}`),
        axios.get(`/api/answers/question/${id}`)
      ]);
      
      setQuestion(questionResponse.data.question);
      setAnswers(answersResponse.data.answers);
    } catch (error) {
      toast.error('Error fetching question');
      navigate('/');
    } finally {
      setLoading(false);
    }
  };

  const handleVote = async (type, itemId, isQuestion = false) => {
    if (!user) {
      toast.error('Please log in to vote');
      return;
    }

    try {
      const endpoint = isQuestion 
        ? `/api/questions/${itemId}/vote`
        : `/api/answers/${itemId}/vote`;
      
      await axios.post(endpoint, { vote: type === 'up' ? 1 : -1 });
      
      // Refresh data
      fetchQuestionAndAnswers();
      toast.success('Vote recorded');
    } catch (error) {
      toast.error(error.response?.data?.message || 'Error recording vote');
    }
  };

  const handleAcceptAnswer = async (answerId) => {
    if (!user) {
      toast.error('Please log in to accept answers');
      return;
    }

    try {
      await axios.post(`/api/questions/${id}/accept-answer/${answerId}`);
      fetchQuestionAndAnswers();
      toast.success('Answer accepted');
    } catch (error) {
      toast.error(error.response?.data?.message || 'Error accepting answer');
    }
  };

  const handleSubmitAnswer = async (e) => {
    e.preventDefault();
    
    if (!answerContent.trim()) {
      toast.error('Please enter an answer');
      return;
    }

    setSubmittingAnswer(true);
    try {
      await axios.post('/api/answers', {
        questionId: id,
        content: answerContent
      });
      
      setAnswerContent('');
      fetchQuestionAndAnswers();
      toast.success('Answer posted successfully!');
    } catch (error) {
      toast.error(error.response?.data?.message || 'Error posting answer');
    } finally {
      setSubmittingAnswer(false);
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = (now - date) / (1000 * 60 * 60);
    
    if (diffInHours < 1) return 'Just now';
    if (diffInHours < 24) return `${Math.floor(diffInHours)}h ago`;
    if (diffInHours < 168) return `${Math.floor(diffInHours / 24)}d ago`;
    return date.toLocaleDateString();
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!question) {
    return (
      <div className="text-center py-12">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Question not found</h2>
        <button
          onClick={() => navigate('/')}
          className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
          Back to Home
        </button>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      {/* Question */}
      <div className="bg-white rounded-lg border p-6 mb-6">
        <div className="flex items-start space-x-4">
          {/* Voting */}
          <div className="flex flex-col items-center space-y-2">
            <button
              onClick={() => handleVote('up', question.id, true)}
              className="p-1 text-gray-400 hover:text-green-600 transition-colors"
              disabled={!user}
            >
              <ThumbsUp className="h-5 w-5" />
            </button>
            <span className="text-lg font-semibold text-gray-900">{question.votes}</span>
            <button
              onClick={() => handleVote('down', question.id, true)}
              className="p-1 text-gray-400 hover:text-red-600 transition-colors"
              disabled={!user}
            >
              <ThumbsDown className="h-5 w-5" />
            </button>
          </div>

          {/* Content */}
          <div className="flex-1">
            <h1 className="text-2xl font-bold text-gray-900 mb-4">{question.title}</h1>
            
            <div 
              className="prose max-w-none mb-4"
              dangerouslySetInnerHTML={{ __html: question.description }}
            />

            {/* Tags */}
            <div className="flex flex-wrap gap-2 mb-4">
              {question.tags.map((tag) => (
                <span
                  key={tag}
                  className="px-2 py-1 bg-blue-100 text-blue-800 text-sm rounded"
                >
                  {tag}
                </span>
              ))}
            </div>

            {/* Meta */}
            <div className="flex items-center justify-between text-sm text-gray-500">
              <div className="flex items-center space-x-4">
                <div className="flex items-center space-x-1">
                  <User className="h-4 w-4" />
                  <span>{question.author?.username}</span>
                </div>
                <div className="flex items-center space-x-1">
                  <Clock className="h-4 w-4" />
                  <span>{formatDate(question.createdAt)}</span>
                </div>
                <div className="flex items-center space-x-1">
                  <MessageSquare className="h-4 w-4" />
                  <span>{answers.length} answers</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Answers */}
      <div className="space-y-6">
        <h2 className="text-xl font-semibold text-gray-900">
          {answers.length} Answer{answers.length !== 1 ? 's' : ''}
        </h2>

        {answers.map((answer) => (
          <div
            key={answer.id}
            className={`bg-white rounded-lg border p-6 ${
              answer.isAccepted ? 'border-green-500 bg-green-50' : ''
            }`}
          >
            <div className="flex items-start space-x-4">
              {/* Voting */}
              <div className="flex flex-col items-center space-y-2">
                <button
                  onClick={() => handleVote('up', answer.id)}
                  className="p-1 text-gray-400 hover:text-green-600 transition-colors"
                  disabled={!user}
                >
                  <ThumbsUp className="h-5 w-5" />
                </button>
                <span className="text-lg font-semibold text-gray-900">{answer.votes}</span>
                <button
                  onClick={() => handleVote('down', answer.id)}
                  className="p-1 text-gray-400 hover:text-red-600 transition-colors"
                  disabled={!user}
                >
                  <ThumbsDown className="h-5 w-5" />
                </button>
              </div>

              {/* Content */}
              <div className="flex-1">
                <div 
                  className="prose max-w-none mb-4"
                  dangerouslySetInnerHTML={{ __html: answer.content }}
                />

                {/* Meta */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-4 text-sm text-gray-500">
                    <div className="flex items-center space-x-1">
                      <User className="h-4 w-4" />
                      <span>{answer.author?.username}</span>
                    </div>
                    <div className="flex items-center space-x-1">
                      <Clock className="h-4 w-4" />
                      <span>{formatDate(answer.createdAt)}</span>
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    {answer.isAccepted && (
                      <div className="flex items-center space-x-1 text-green-600">
                        <CheckCircle className="h-5 w-5" />
                        <span className="text-sm font-medium">Accepted</span>
                      </div>
                    )}
                    
                    {user && question.authorId === user.id && !answer.isAccepted && (
                      <button
                        onClick={() => handleAcceptAnswer(answer.id)}
                        className="px-3 py-1 text-sm bg-green-600 text-white rounded hover:bg-green-700 transition-colors"
                      >
                        Accept
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}

        {/* No answers */}
        {answers.length === 0 && (
          <div className="text-center py-12 bg-white rounded-lg border">
            <MessageSquare className="mx-auto h-12 w-12 text-gray-400 mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">No answers yet</h3>
            <p className="text-gray-500 mb-4">
              Be the first to answer this question!
            </p>
          </div>
        )}
      </div>

      {/* Answer Form */}
      {user ? (
        <div className="bg-white rounded-lg border p-6 mt-8">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Your Answer</h3>
          <form onSubmit={handleSubmitAnswer}>
            <ReactQuill
              value={answerContent}
              onChange={setAnswerContent}
              placeholder="Write your answer here..."
              className="mb-4"
            />
            <div className="flex justify-end space-x-4">
              <button
                type="submit"
                disabled={submittingAnswer || !answerContent.trim()}
                className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {submittingAnswer ? 'Posting...' : 'Post Answer'}
              </button>
            </div>
          </form>
        </div>
      ) : (
        <div className="bg-white rounded-lg border p-6 mt-8 text-center">
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Want to answer?</h3>
          <p className="text-gray-500 mb-4">
            Please log in to post an answer to this question.
          </p>
          <button
            onClick={() => navigate('/login')}
            className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            Log In
          </button>
        </div>
      )}
    </div>
  );
};

export default QuestionDetail; 